import { Box, LinearProgress, ToggleButton, ToggleButtonGroup } from '@mui/material';
import React from 'react';
import logo from './logo.png'
import { useAsync } from 'react-async';
import { init, StartUpData } from './util';
import { useRoutes } from 'react-router';
import FileListView from './FileListView';

function ButtonGroup(props: ButtonGroupProps) {
  return (
    <ToggleButtonGroup
      color='primary'
      size='small'
      value={props.selected}
      onChange={(event, newValues) => {props.onChange(newValues);}}
      aria-label="text formatting"
      sx={{ display: 'flex', flexWrap: 'wrap', padding: '0.5em'}}
      > 
        {props.values.map((value) => <ToggleButton key={value} value={value}>{value}</ToggleButton>)}
      </ToggleButtonGroup>
  );
}

export function Home(props: StartUpData) {
  const [selectedUniversities, setSelectedUniversities] = React.useState<string[]>([]);
  const [selectedMajors, setSelectedMajors] = React.useState<string[]>([]);
  const [selectedSubjects, setSelectedSubjects] = React.useState<string[]>([]);

  props.routes.forEach(e => {
    console.log(e.path);
  })

  const onChange = (newValues: string[], setValues: React.Dispatch<React.SetStateAction<string[]>>) => {
    setValues(newValues);
  }
  return (
    <Box sx={{width: {sm: '60%' }, margin: 'auto'}}>
      <Box sx={{width: '50%', margin: 'auto'}}><img src={logo} alt='Logo' width='100%' /></Box>
      <ButtonGroup 
        values={Array.from(props.allUniversities)} 
        selected={selectedUniversities} 
        onChange={(values) => {onChange(values, setSelectedUniversities)}} 
      />
      <ButtonGroup 
        values={Array.from(props.allMajors)} 
        selected={selectedMajors}
        onChange={(values) => {onChange(values, setSelectedMajors)}} 
      />
      <ButtonGroup 
        values={Array.from(props.allSubjects)} 
        selected={selectedSubjects}
        onChange={(values) => {onChange(values, setSelectedSubjects)}} 
      />
      <FileListView
        key={selectedUniversities.toString()+selectedMajors.toString()+selectedSubjects.toString()} 
        universities={selectedUniversities} 
        majors={selectedMajors} 
        subjects={selectedSubjects} 
      /> 
    </Box>
  );
}

